import React from 'react'
import './GoodsListItem.css'

const GoodsListItem = ({ title, author, price, inCart, src, addToCart }) => {
    return (
        <div className="GoodsListItem card bg-light mt-3">
            <div className="card-body row">
                <div className="col-md-3 col-sm-4 col-12">
                    <div className="GoodsListItemImage text-center">
                        <img src={src} alt="Eloquent javascript"/>
                    </div>
                </div>
                <div className="col-md-9 col-sm-8 col-12 mt-3 mt-sm-0">
                    <div className="GoodsTitle">
                        <h4>{title}</h4>
                        <small>{author}</small>
                        <hr/>
                    </div>
                    
                    <div className="GoodsPrice mt-3">
                        <div className="d-inline-flex flex-column">
                            <b>${price}</b>
                            {
                                inCart
                                    ? <button className="btn btn-danger" onClick={addToCart}>Remove from cart</button>
                                    : <button className="btn btn-primary" onClick={addToCart}>Add to cart</button>
                            }
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default GoodsListItem