import React from 'react'
import './CartItem.css'

const CartItem = ({ title, price, count, total, onInc, onDec, onRemove }) => {
    
    return (
        <tr className="CartItem">
            <td title={title}>
                <p>{title}</p>
            </td>
            <td>${price}</td>
            <td>
                <button className="btn btn-outline-secondary count-button" onClick={onDec}>
                    <i className="fa fa-minus"></i>
                </button>
                {count}
                <button className="btn btn-outline-secondary count-button" onClick={onInc}>
                    <i className="fa fa-plus"></i>
                </button>
            </td>
            <td>
                ${total}
            </td>
            <td>
                <button className="btn btn-outline-danger remove-button" onClick={onRemove}>
                    <i className="fa fa-trash"></i>
                </button>
            </td>
        </tr>
    )
}

export default CartItem