import React from 'react'
import './GoodsList.css'

import GoodsListItem from '../GoodsListItem/GoodsListItem'
import { incCount } from '../../actions'
import { useSelector, useDispatch } from 'react-redux'

const GoodsList = () => {
    const { goodsList } = useSelector(goodsList => goodsList)
    const dispatch = useDispatch()

    return (
        <div className="GoodsList">
            {
                goodsList.map(({ id, title, author, price, inCart, src }) => (
                    <GoodsListItem
                        key={id}
                        id={id}
                        title={title}
                        author={author}
                        price={price}
                        inCart={inCart}
                        src={src}
                        addToCart={() => dispatch(incCount(id))}
                    />
                ))
            }
        </div>
    )
}

export default GoodsList