import React from 'react'
import './Cart.css'
import CartItem from '../CartItem/CartItem'

import swal from 'sweetalert'
import { useSelector, useDispatch } from 'react-redux'
import { incCount, decCount, buyItems, clearCart, removeItem } from '../../actions'

const Cart = () => {

    const { cart, totalPrice } = useSelector(({ cart, totalPrice }) => ({ cart, totalPrice }))
    const dispatch = useDispatch()

    if(!cart.length) {
        return <h2 className="text-center">Cart is empty!</h2>
    }

    return (
        <div className="Cart">
            <table className="table">
                <thead>
                    <tr>
                        <th scope="col">Title</th>
                        <th scope="col">Price</th>
                        <th scope="col">Count</th>
                        <th scope="col">Total</th>
                        <th scope="col">Remove</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        cart.map(({ id, title, price, count, total }) => (
                            <CartItem
                                key={id}
                                title={title}
                                price={price}
                                count={count}
                                total={total}
                                onInc={() => dispatch(incCount(id))}
                                onDec={() => dispatch(decCount(id))}
                                onRemove={() => dispatch(removeItem(id))}
                            />
                        ))
                    }
                </tbody>
            </table>
            <div className="CartBottom">
                <div className="CartButtons">
                    <button
                        className="btn btn-success"
                        onClick={() => {
                            swal({ title: 'Are you sure?', icon: 'info', buttons: true })
                                .then(buy => buy ? dispatch(buyItems()) : null)
                        }}
                    >
                        Buy
                    </button>
                    <button
                        className="btn btn-danger"
                        onClick={() => {
                            swal({ title: 'Are you sure?', icon: 'warning', buttons: true })
                                .then(buy => buy ? dispatch(clearCart()) : null)
                        }}
                    >
                        Clear
                    </button>
                </div>
                <h4>Total price: ${totalPrice}</h4>
            </div>
        </div>
    )
}

export default Cart