const initialState = {
    goodsList: [
        { id: 0, title: "Eloquent JavaScript", author: "Marijn Haverbeke", price: 25, src: "https://eloquentjavascript.net/img/cover.jpg" },
        { id: 1, title: "CLR via C#", author: "Jeffrey Richter", price: 59, src: "https://cdn1.ozone.ru/multimedia/c500/1007028713.jpg" },
        { id: 2, title: "Learning React: Functional Web Development with React and Redux", author: "Alex Banks, Eve Porcello", price: 39, src: "https://images-na.ssl-images-amazon.com/images/I/7169mYAhsmL._AC_UL600_SR456,600_.jpg" },
        { id: 3, title: "You Don't Know JS: Scope & Closures", author: "Kyle Simpson", price: 20, src: "https://cv01.twirpx.net/1519/1519642.jpg?t=20141023202345" },
        { id: 4, title: "CSS: The Missing Manual", author: "David Sawyer McFarland", price: 25, src: "https://images-na.ssl-images-amazon.com/images/I/81qLG+SyGfL.jpg" },
    ],
    cart: [],
    totalPrice: 0
}

const updateCart = (cart, item, idx) => {

    if(idx === -1) {
        return [
            ...cart,
            item
        ]
    }

    if(item.count < 1) {
        return [
            ...cart.slice(0, idx),
            ...cart.slice(idx + 1)
        ]
    }

    return [
        ...cart.slice(0, idx),
        item,
        ...cart.slice(idx + 1)
    ]
}

const updateCartItem = (good, cartItem = {}, quantity) => {
    const {
        id = good.id,
        title = good.title,
        price = good.price,
        count = 0,
        total = 0
    } = cartItem

    return {
        id,
        title,
        price,
        count: count + quantity,
        total: total + price * quantity
    }
}

const updateOrder = (state, itemId, quantity) => {
    const { goodsList, cart } = state

    const good = goodsList.find(({ id }) => id === itemId)
    const itemIdx = cart.findIndex(({ id }) => id === itemId)
    const item = cart[itemIdx]

    const newItem = updateCartItem(good, item, quantity)
    const newCart = updateCart(cart, newItem, itemIdx)

    return {
        ...state,
        cart: newCart,
        totalPrice: newCart.reduce((sum, item) => sum + item.total, 0)
    }
}

const reducer = (state = initialState, { type, payload }) => {
    switch(type) {
        case 'INC_COUNT':
            return updateOrder(state, payload, 1)
        case 'DEC_COUNT':
            return updateOrder(state, payload, -1)
        case 'REMOVE_ITEM':
            const item = state.cart.find(({ id }) => id === payload)
            return updateOrder(state, payload, -item.count)
        case 'CLEAR_CART':
            return {
                ...state,
                goodsList: state.goodsList.map(item => ({ ...item, inCart: false })),
                cart: []
            }
        case 'BUY_ITEMS':
            return {
                ...state,
                goodsList: state.goodsList.map(item => ({ ...item, inCart: false })),
                cart: []
            }
        default:
            return state
    }
}

export default reducer