const changeInCart = id => ({
    type: 'CHANGE_IN_CART',
    payload: id
})

const incCount = id => ({
    type: 'INC_COUNT',
    payload: id
})

const decCount = id => ({
    type: 'DEC_COUNT',
    payload: id
})

const removeItem = id => ({
    type: 'REMOVE_ITEM',
    payload: id
})

const clearCart = () => ({
    type: 'CLEAR_CART'
})

const buyItems = () => ({
    type: 'BUY_ITEMS'
})

export { changeInCart, incCount, decCount, removeItem, clearCart, buyItems }